const contentful = require('contentful')

export const getHomeContent = async () => {
  const client = contentful.createClient({
    space: '3ikropa9n02v',
    environment: 'master', // defaults to 'master' if not set
    accessToken: 'TYyXZKWmocjDeGaPG3lksEc5hxDFrYP2SGGjpfN5te0'
  })
  const response = await client.getEntries({content_type: 'homeContent'})
  return response.items[0].fields
}

export const getCVContent = async () => {
  const client = contentful.createClient({
    space: '3ikropa9n02v',
    environment: 'master', // defaults to 'master' if not set
    accessToken: 'TYyXZKWmocjDeGaPG3lksEc5hxDFrYP2SGGjpfN5te0'
  })
  const response = await client.getEntries({content_type: 'file'})
  return response.items[0].fields.file.fields.file.url
}  