import React from 'react';
import Header from '../Header';
import CVIframe from './cvIframe';
import './index.css'

const Projects = () => {
    return(
        <div className="content">
            <Header/>
            <div className="info cv-wrapper">
                <div className="title">
                    <h2>My CV</h2>
                </div>
                <CVIframe />
            </div>
        </div>
    )
}

export default Projects;