import React from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from 'react-router-dom';
import Projects from './Projects';
import Home from './Home'
import Contacts from './Contacts';
import CV from './CV';

function App() {
  return (
    <Router>
      <Switch>
        <Route path='/my-app'  render={Home} exact/>
        <Route path='/my-app/projects'  render={Projects} />
        <Route path='/my-app/contacts'  render={Contacts} />
        <Route path='/my-app/cv'  render={CV} />
      </Switch>
    </Router>
  );
}

export default App;
