import React, {useState, useEffect} from 'react';
import { getCVContent } from '../contentful';

function CVIframe() {
    const [cvContent, setCvContent] = useState()
    const fetchHomeContent = async () => {
        const cvContent = await getCVContent()
        setCvContent(cvContent)
  
    }
    
    useEffect(() => {
      fetchHomeContent()
    }, [])

    return <iframe title='cv' src={`${cvContent}#view=Fit`} width="100%" height="100%" allowFullScreen ></iframe>
}

export default CVIframe