import React from 'react';
import './index.css';
import { Image} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Link
} from 'react-router-dom';
import { useState } from 'react';
import HamburgerMenuIcon from '../Icons/hamburger-menu.svg'
import CloseMenuIcon from '../Icons/cross.svg'
import useWindowDimensions from '../hooks/useWindowDimensions'

const Header = () => {
  const [isOpen, setIsOpen] = useState(false)
  const { width } = useWindowDimensions()

  return isOpen || width >= 1024 ? (
    <div  className="nav" >
      <div className="inner">
        {width < 1024  && <img className='close-menu-button' src={CloseMenuIcon} onClick={() => setIsOpen(false)} style={{cursor: 'pointer'}} alt='close-menu'/>}
        <div className='image-container'>
          <Image width={width < 1024 ? '50%' : '100%'}  src="/my-app/Picture3.jpg" roundedCircle />
        </div>
        {width < 1024 ? (
          <ul className="header-menu">
            <li onClick={() => setIsOpen(false)}><Link to="/my-app">About</Link></li>
            <li onClick={() => setIsOpen(false)}><Link to="/my-app/projects">Projects</Link></li>
            <li onClick={() => setIsOpen(false)}><Link to="/my-app/contacts">Contacts</Link></li>
            <li onClick={() => setIsOpen(false)}><Link to="/my-app/cv">CV</Link></li>
          </ul> 
          ) : (
          <ul className="header-menu">
            <li className={window.location.pathname === '/my-app' && 'active-link'}><Link to="/my-app">About</Link></li>
            <li className={window.location.pathname.includes('projects') && 'active-link'}><Link to="/my-app/projects">Projects</Link></li>
            <li className={window.location.pathname.includes('contacts') && 'active-link'}><Link to="/my-app/contacts">Contacts</Link></li>
            <li className={window.location.pathname.includes('cv') && 'active-link'}><Link to="/my-app/cv">CV</Link></li>
          </ul>
        )}
      </div>
    </div>
  ) : (
      <div style={{display: 'flex', justifyContent: 'space-between', padding: '20px'}}>
        <Image width={`70px`}  src="/my-app/Picture3.jpg" roundedCircle  alt='vasil-ivanov' style={{cursor: 'pointer'}}  onClick={() => setIsOpen(true)}/>
        <img src={HamburgerMenuIcon} onClick={() => setIsOpen(true)} style={{cursor: 'pointer'}} alt='hamburger-menu'/>
      </div>
    )
}

export default Header;